<template>
  <div id="Attended">
    <my-service-p v-for="(item,index) in concern_data" :key=index :data="item"
                  @clickC="cancelConcern(index)"></my-service-p>
  </div>
</template>

<script>
import myServiceP from '../../../../../components/myServiceP'
import myUtil from '../../../../../common/myUtil'

export default {
  name: 'Attended',
  components: {
    myServiceP
  },
  data () {
    return {
      concern_data: [
        {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '112',
          concerned: true
        }, {
          name: '服务人员',
          online: true,
          id: '112',
          reputably: '112',
          concerned: true
        }
      ]
    }
  },
  mounted () {
    this.query()
  },
  query () {
    this.getSubscribeAdvisor()
  },
  getSubscribeAdvisor () {
    this.$http.getSubscribeAdvisor({}).then({})
  },
  methods: {
    cancelConcern (index) {
      if (this.concern_data[index].concerned) {
        this.concern_data[index].concerned = false
      } else {
        this.concern_data[index].concerned = true
      }
      this.query()
    },
    query () {
      this.concern_data = myUtil.sortObject(this.concern_data, 'reputably')
    }
  }
}
</script>

<style scoped>
  #Attended {
  }
</style>



